/**
 * MyMemoryAPI - API
 */
async function translateWithMyMemory(text, targetLang = 'en', sourceLang = 'auto') {
  try {
    // MyMemory - translators
    const langMap = {
      // 
      'zh': 'zh-CN', 
      'en': 'en-US', 
      'ja': 'ja-JP', 
      'ko': 'ko-KR',
      'fr': 'fr-FR', 
      'de': 'de-DE', 
      'es': 'es-ES', 
      'ru': 'ru-RU',
      'it': 'it-IT',
      'pt': 'pt-PT',
      'ar': 'ar-SA',
      'hi': 'hi-IN',
      'th': 'th-TH',
      'vi': 'vi-VN',
      'tr': 'tr-TR',
      'pl': 'pl-PL',
      'nl': 'nl-NL',
      'sv': 'sv-SE',
      'da': 'da-DK',
      'no': 'no-NO',
      'fi': 'fi-FI',
      'el': 'el-GR',
      'hu': 'hu-HU',
      'cs': 'cs-CZ',
      'sk': 'sk-SK',
      'hr': 'hr-HR',
      'bg': 'bg-BG',
      'ro': 'ro-RO',
      'et': 'et-EE',
      'lv': 'lv-LV',
      'lt': 'lt-LT',
      'sl': 'sl-SI',
      'mt': 'mt-MT',
      'is': 'is-IS',
      'ga': 'ga-IE',
      'cy': 'cy-GB',
      'eu': 'eu-ES',
      'ca': 'ca-ES',
      'auto': 'autodetect'
    };
    
    const sourceLangCode = sourceLang === 'auto' ? 'autodetect' : (langMap[sourceLang] || sourceLang);
    const targetLangCode = langMap[targetLang] || targetLang;
    
    const langPair = sourceLangCode === 'autodetect' ? `autodetect|${targetLangCode}` : `${sourceLangCode}|${targetLangCode}`;
    const url = `${TRANSLATION_APIS.mymemory.baseUrl}/get?q=${encodeURIComponent(text)}&langpair=${langPair}&de=support@i-prompt.com`;
    
    console.log(`MyMemory API: ${url}`);
    
    const response = await fetch(url, {
      method: 'GET',
      headers: {
        'User-Agent': 'I-Prompt/3.0 Translation Service',
        'Accept': 'application/json'
      }
    });
    
    if (!response.ok) {
      throw new Error(`HTTP ${response.status}: ${response.statusText}`);
    }
    
    const data = await response.json();
    console.log('MyMemory:', data);
    
    if (data.responseStatus === 200 && data.responseData) {
      const translatedText = data.responseData.translatedText;
      
      // 
      if (translatedText && translatedText.trim() !== text.trim()) {
        return {
          translatedText: translatedText,
          confidence: data.responseData.match || 'medium',
          source: 'mymemory',
          note: `MyMemory ${sourceLangCode}  ${targetLangCode}`
        };
      }
    }
    
    throw new Error(data.responseDetails || '');
  } catch (error) {
    console.error('MyMemory:', error);
    // 
    return await simulateTranslation(text, 'mymemory', targetLang);
  }
}

/**
 * LibreTranslateAPI - 
 */
async function translateWithLibre(text, targetLang = 'en', sourceLang = 'auto') {
  try {
    // LibreTranslate - 
    const langMap = {
      // LibreTranslate
      'zh': 'zh', 
      'en': 'en', 
      'ja': 'ja', 
      'ko': 'ko',
      'fr': 'fr', 
      'de': 'de', 
      'es': 'es', 
      'ru': 'ru',
      'it': 'it',
      'pt': 'pt',
      'ar': 'ar',
      'hi': 'hi',
      'tr': 'tr',
      'pl': 'pl',
      'nl': 'nl',
      'sv': 'sv',
      'da': 'da',
      'fi': 'fi',
      'el': 'el',
      'hu': 'hu',
      'cs': 'cs',
      'sk': 'sk',
      'bg': 'bg',
      'et': 'et',
      'lv': 'lv',
      'lt': 'lt',
      'sl': 'sl',
      'mt': 'mt',
      'ga': 'ga',
      'cy': 'cy',
      'eu': 'eu',
      'ca': 'ca'
    };
    
    const sourceLangCode = sourceLang === 'auto' ? 'auto' : (langMap[sourceLang] || 'auto');
    const targetLangCode = langMap[targetLang] || 'en';
    
    console.log(`LibreTranslate: ${sourceLangCode} -> ${targetLangCode}`);
    
    const requestData = {
      q: text,
      source: sourceLangCode,
      target: targetLangCode,
      format: 'text'
    };
    
    const response = await fetch(`${TRANSLATION_APIS.libre.baseUrl}`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'User-Agent': 'I-Prompt/3.0 Translation Service',
        'Accept': 'application/json'
      },
      body: JSON.stringify(requestData)
    });
    
    if (!response.ok) {
      throw new Error(`HTTP ${response.status}: ${response.statusText}`);
    }
    
    const data = await response.json();
    console.log('LibreTranslate:', data);
    
    if (data.translatedText) {
      const translatedText = data.translatedText;
      
      // 
      if (translatedText && translatedText.trim() !== text.trim()) {
        return {
          translatedText: translatedText,
          confidence: 'medium',
          source: 'libretranslate',
          note: `LibreTranslate ${sourceLangCode}  ${targetLangCode}`
        };
      }
    }
    
    throw new Error('LibreTranslate');
  } catch (error) {
    console.error('LibreTranslate:', error);
    // 
    return await simulateTranslation(text, 'libre', targetLang);
  }
}

/**
 * Google WebAPI - API
 */
async function translateWithGoogleWeb(text, targetLang = 'en', sourceLang = 'auto') {
  try {
    // Google - translators
    const langMap = {
      // Google
      'zh': 'zh-cn', 
      'en': 'en', 
      'ja': 'ja', 
      'ko': 'ko',
      'fr': 'fr', 
      'de': 'de', 
      'es': 'es', 
      'ru': 'ru',
      'it': 'it',
      'pt': 'pt',
      'ar': 'ar',
      'hi': 'hi',
      'th': 'th',
      'vi': 'vi',
      'tr': 'tr',
      'pl': 'pl',
      'nl': 'nl',
      'sv': 'sv',
      'da': 'da',
      'no': 'no',
      'fi': 'fi',
      'el': 'el',
      'hu': 'hu',
      'cs': 'cs',
      'sk': 'sk',
      'hr': 'hr',
      'bg': 'bg',
      'ro': 'ro',
      'et': 'et',
      'lv': 'lv',
      'lt': 'lt',
      'sl': 'sl',
      'mt': 'mt',
      'is': 'is',
      'ga': 'ga',
      'cy': 'cy',
      'eu': 'eu',
      'ca': 'ca',
      'auto': 'auto'
    };
    
    const sourceLangCode = langMap[sourceLang] || 'auto';
    const targetLangCode = langMap[targetLang] || 'en';
    
    // Google TranslateAPI URL
    const params = new URLSearchParams({
      client: 'gtx',
      sl: sourceLangCode,
      tl: targetLangCode,
      dt: 't',
      q: text
    });
    
    const url = `${TRANSLATION_APIS.google_web.baseUrl}?${params.toString()}`;
    console.log(`Google Web API: ${sourceLangCode} -> ${targetLangCode}`);
    
    const response = await fetch(url, {
      method: 'GET',
      headers: {
        'User-Agent': 'Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/120.0.0.0 Safari/537.36',
        'Accept': '*/*',
        'Accept-Language': 'en-US,en;q=0.9',
        'Accept-Encoding': 'gzip, deflate, br',
        'Connection': 'keep-alive',
        'Referer': 'https://translate.google.com/'
      }
    });
    
    if (!response.ok) {
      throw new Error(`HTTP ${response.status}: ${response.statusText}`);
    }
    
    const responseText = await response.text();
    console.log('Google:', responseText.length);
    
    // GoogleJSON
    try {
      const data = JSON.parse(responseText);
      console.log('Google:', data);
      
      if (data && data[0] && data[0][0] && data[0][0][0]) {
        const translatedText = data[0][0][0];
        
        // 
        if (translatedText && translatedText.trim() !== text.trim()) {
          return {
            translatedText: translatedText,
            confidence: 'high',
            source: 'google_web',
            note: `Google ${sourceLangCode}  ${targetLangCode}`
          };
        }
      }
    } catch (parseError) {
      console.warn('Google:', parseError);
    }
    
    throw new Error('Google');
  } catch (error) {
    console.error('Google:', error);
    // 
    return await simulateTranslation(text, 'google_web', targetLang);
  }
}

